"use client";

import { useState, useEffect, useRef } from "react";
import DestinationCard from "./DestinationCard";
import "../styles/MonthlyPackages.css";
import AnimatedElement from "./AnimatedElement";

function MonthlyDestinations() {
  const [currentMonth, setCurrentMonth] = useState(new Date().getMonth());
  const [isPaused, setIsPaused] = useState(false);
  const [autoScrollEnabled, setAutoScrollEnabled] = useState(true);
  const [scrollDirection, setScrollDirection] = useState("right");
  const [canScrollLeft, setCanScrollLeft] = useState(false);
  const [canScrollRight, setCanScrollRight] = useState(true);
  const [isTouchScrolling, setIsTouchScrolling] = useState(false);
  const [packagesData, setPackagesData] = useState({
    packages: [],
    destinations: [],
  });
  const [loading, setLoading] = useState(true);
  const destinationsRef = useRef(null);

  const months = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];

  // Map of best destinations for each month (0-11 for Jan-Dec)
  const monthlyDestinations = {
    0: ["Goa", "Jaipur", "Kerala", "Maldives"], // January
    1: ["Andaman", "Rajasthan", "Bali", "Goa"], // February
    2: ["Sikkim", "Darjeeling", "Andaman", "Vietnam"], // March
    3: ["Manali", "Rishikesh", "Ooty", "Bali"], // April
    4: ["Shimla", "Manali", "Mussoorie", "Darjeeling"], // May
    5: [
      "Ladakh",
      "Spiti Valley",
      "Coorg",
      "Munnar",
      "Goa",
      "Andaman",
      "Rajasthan",
    ], // June
    6: [
      "Ladakh",
      "Valley of Flowers",
      "Meghalaya",
      "Maldives",
      "Kashmir",
      "Kerala",
      "Shimla",
      "Manali",
    ], // July
    7: ["Ladakh", "Kerala", "Rajasthan", "Bali"], // August
    8: ["Andaman", "Goa", "Kerala", "Vietnam"], // September
    9: ["Rajasthan", "Goa", "Andaman", "Bali"], // October
    10: ["Goa", "Andaman", "Kerala", "Maldives"], // November
    11: ["Goa", "Andaman", "Rajasthan", "Bali"], // December
  };

  // Fetch packages data from API
  useEffect(() => {
    const fetchPackagesData = async () => {
      try {
        const response = await fetch("/api/packages");
        const data = await response.json();

        if (data.success) {
          setPackagesData({
            packages: data.packages || [],
            destinations: data.destinations || [],
          });
        } else {
          console.error("Failed to fetch packages data:", data.message);
        }
      } catch (error) {
        console.error("Error fetching packages data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchPackagesData();
  }, []);

  // Function to calculate package count for a destination
  const getPackageCountForDestination = (destination) => {
    // Extract meaningful words from destination name
    const destinationName = destination.name.toLowerCase();

    // Split the destination name into words and filter out common words
    const commonWords = [
      "india",
      "the",
      "and",
      "&",
      "of",
      "in",
      "at",
      "to",
      "for",
      "with",
      "by",
      "a",
      "an",
      "escape",
      "retreat",
      "tour",
      "adventure",
      "getaway",
      "vacation",
      "holiday",
      "trip",
      "experience",
      "expedition",
      "journey",
      "splendor",
      "bliss",
      "explorer",
      "package",
      "packages",
    ];
    const locationWords = destinationName
      .split(/[\s,&-]+/) // Split by spaces, commas, ampersands, and hyphens
      .filter((word) => word.length > 2 && !commonWords.includes(word)); // Filter out common words and short words

    // Count packages that match any of the location words
    const matchingPackages = packagesData.packages.filter((pkg) => {
      if (!pkg.location) return false;

      const packageLocation = pkg.location.toLowerCase();
      const packageName = pkg.name.toLowerCase();

      // Check if any of the location words appear in the package location or name
      return locationWords.some(
        (word) => packageLocation.includes(word) || packageName.includes(word)
      );
    });

    return matchingPackages.length;
  };

  // Filter destinations based on current month's best destinations
  const getMonthlyDestinations = (month) => {
    const bestDestinations = monthlyDestinations[month] || [];
    return packagesData.destinations.filter((dest) =>
      bestDestinations.some((bestDest) =>
        dest.name.toLowerCase().includes(bestDest.toLowerCase())
      )
    );
  };

  const [monthlyDestinationsList, setMonthlyDestinationsList] = useState([]);

  useEffect(() => {
    if (!loading && packagesData.destinations.length > 0) {
      const destinations = getMonthlyDestinations(currentMonth);
      setMonthlyDestinationsList(destinations);

      // Reset scroll position when month changes
      if (destinationsRef.current) {
        destinationsRef.current.scrollLeft = 0;
      }

      // Update scroll indicators after a short delay to ensure DOM is updated
      setTimeout(() => {
        checkScrollPosition();
      }, 100);
    }
  }, [currentMonth, packagesData, loading]);

  const handleMonthChange = (month) => {
    setCurrentMonth(month);
  };

  const scroll = (direction) => {
    if (!destinationsRef || !destinationsRef.current) return;

    // Stop auto-scrolling when manually scrolling
    setAutoScrollEnabled(false);

    // Calculate the width of a single card (250px) plus gap (20px)
    const cardWidth = 270;
    const scrollAmount = direction === "left" ? -cardWidth : cardWidth;
    destinationsRef.current.scrollBy({
      left: scrollAmount,
      behavior: "smooth",
    });

    // Update scroll position indicators after scrolling
    setTimeout(() => {
      checkScrollPosition();
      // Resume auto-scrolling after a delay
      setTimeout(() => {
        setAutoScrollEnabled(true);
      }, 2000);
    }, 300);
  };

  // Check if scroll has reached the end or beginning
  const checkScrollPosition = () => {
    if (!destinationsRef || !destinationsRef.current) return;

    const { scrollLeft, scrollWidth, clientWidth } = destinationsRef.current;

    // Update scroll indicators
    setCanScrollLeft(scrollLeft > 10);
    setCanScrollRight(scrollLeft + clientWidth < scrollWidth - 10);

    // Set scroll direction for auto-scroll functionality
    if (scrollLeft + clientWidth >= scrollWidth - 10) {
      setScrollDirection("left");
    } else if (scrollLeft <= 10 && scrollDirection === "left") {
      setScrollDirection("right");
    }
  };

  // Handle touch events for mobile devices
  useEffect(() => {
    const handleTouchStart = () => {
      setIsTouchScrolling(true);
      setAutoScrollEnabled(false);
    };

    const handleTouchEnd = () => {
      // Add a delay before resuming auto-scroll
      setTimeout(() => {
        setIsTouchScrolling(false);
        setAutoScrollEnabled(true);
      }, 1500);
    };

    // Add touch event listeners to scroll container
    if (destinationsRef.current) {
      destinationsRef.current.addEventListener("touchstart", handleTouchStart, {
        passive: true,
      });
      destinationsRef.current.addEventListener("touchend", handleTouchEnd, {
        passive: true,
      });
    }

    // Clean up event listeners
    return () => {
      if (destinationsRef.current) {
        destinationsRef.current.removeEventListener(
          "touchstart",
          handleTouchStart
        );
        destinationsRef.current.removeEventListener("touchend", handleTouchEnd);
      }
    };
  }, []);

  // Add scroll event listener
  useEffect(() => {
    const handleScroll = () => {
      checkScrollPosition();

      // If user is manually scrolling, pause auto-scroll
      if (!isTouchScrolling) {
        setAutoScrollEnabled(false);

        // Resume auto-scrolling after a delay
        clearTimeout(window.monthlyScrollTimeout);
        window.monthlyScrollTimeout = setTimeout(() => {
          setAutoScrollEnabled(true);
        }, 2000);
      }
    };

    if (destinationsRef.current) {
      destinationsRef.current.addEventListener("scroll", handleScroll, {
        passive: true,
      });
    }

    return () => {
      if (destinationsRef.current) {
        destinationsRef.current.removeEventListener("scroll", handleScroll);
      }
      clearTimeout(window.monthlyScrollTimeout);
    };
  }, [scrollDirection, isTouchScrolling]);

  // Auto-scroll functionality
  useEffect(() => {
    if (
      !autoScrollEnabled ||
      monthlyDestinationsList.length === 0 ||
      isTouchScrolling
    )
      return;

    const scrollDestinations = () => {
      if (destinationsRef.current && !isPaused) {
        const cardWidth = 1; // Scroll 1px at a time for smooth scrolling

        destinationsRef.current.scrollBy({
          left: scrollDirection === "right" ? cardWidth : -cardWidth,
          behavior: "auto",
        });

        checkScrollPosition();
      }
    };

    const scrollInterval = setInterval(scrollDestinations, 30);

    return () => clearInterval(scrollInterval);
  }, [
    isPaused,
    monthlyDestinationsList,
    autoScrollEnabled,
    scrollDirection,
    isTouchScrolling,
  ]);

  if (loading) {
    return (
      <section
        className="monthly-destinations section"
        id="monthly-destinations"
      >
        <div className="container">
          <div className="section-header">
            <h2 className="section-title">Loading destinations...</h2>
          </div>
        </div>
      </section>
    );
  }

  return (
    <section className="monthly-destinations section" id="monthly-destinations">
      <div className="container">
        <AnimatedElement animation="fade-up">
          <div className="section-header">
            <h2 className="section-title">
              Best Destinations for {months[currentMonth]}
            </h2>
            <p className="section-subtitle">
              Discover the perfect places to visit this month
            </p>
          </div>
        </AnimatedElement>

        <AnimatedElement animation="fade-up" delay={200}>
          <div className="month-selector">
            {months.map((month, index) => (
              <button
                key={index}
                className={`month-btn ${
                  index === currentMonth ? "active" : ""
                }`}
                onClick={() => handleMonthChange(index)}
              >
                {month}
              </button>
            ))}
          </div>
        </AnimatedElement>

        <AnimatedElement animation="fade-up" delay={400}>
          <div className="monthly-destinations-container">
            <div className="scroll-buttons">
              <button
                className={`scroll-button ${!canScrollLeft ? "disabled" : ""}`}
                onClick={() => scroll("left")}
                aria-label="Scroll left"
                disabled={!canScrollLeft}
              >
                <i className="fas fa-chevron-left"></i>
              </button>
              <button
                className={`scroll-button ${!canScrollRight ? "disabled" : ""}`}
                onClick={() => scroll("right")}
                aria-label="Scroll right"
                disabled={!canScrollRight}
              >
                <i className="fas fa-chevron-right"></i>
              </button>
            </div>

            <div className="destinations-scroll-container">
              <div
                className="destinations-scroll"
                ref={destinationsRef}
                onMouseEnter={() => setIsPaused(true)}
                onMouseLeave={() => setIsPaused(false)}
              >
                {monthlyDestinationsList.length > 0 ? (
                  monthlyDestinationsList.map((destination, index) => {
                    // Create a new destination object with dynamic count
                    const destinationWithDynamicCount = {
                      ...destination,
                      count: getPackageCountForDestination(destination),
                    };

                    return (
                      <DestinationCard
                        key={`${destination.id}-${index}`}
                        destination={destinationWithDynamicCount}
                      />
                    );
                  })
                ) : (
                  <div className="no-destinations">
                    <i className="fas fa-exclamation-circle"></i>
                    <p>
                      No destinations available for this month. Please check
                      other months.
                    </p>
                  </div>
                )}
              </div>
            </div>
          </div>
        </AnimatedElement>
      </div>
    </section>
  );
}

export default MonthlyDestinations;
